html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }
  body.fix-body {
    overflow: hidden; }

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
h2,
h3 {
  margin: 0; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.bg-green {
  background-color: #4cb8b3; }

.bg-yellow {
  background-color: #fef46d; }

.header {
  background-color: #4cb8b3; }
  .header .header-content {
    padding: 2.5rem 0; }
    @media (min-width: 768px) {
      .header .header-content {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: #f7f7f7;
    text-align: center;
    margin-bottom: 1rem; }
    @media (min-width: 768px) {
      .header h1 {
        margin-bottom: 0; } }
  .header.fix {
    position: fixed;
    width: 100%;
    box-shadow: 0 0.3rem 0.4rem #444; }

.main-nav {
  display: flex;
  gap: 2rem;
  justify-content: center; }
  .main-nav a {
    color: #f7f7f7;
    font-size: 2rem; }
    .main-nav a:hover {
      color: #fef46d; }

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #f53756;
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 63%, rgba(255, 255, 255, 0.5) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 63%, rgba(255, 255, 255, 0.5) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.61) 63%, rgba(255, 255, 255, 0.5) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video video {
    display: block;
    width: 100%; }

.video-content {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1; }
  .video-content h2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #f7f7f7;
    font-size: 7rem; }
    .video-content h2 span {
      background-color: #f53756;
      font-size: 2rem;
      padding: 1rem 2rem; }

.about-festival {
  padding: 5rem 0; }
  @media (min-width: 768px) {
    .about-festival {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 2rem; } }

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .festival-content h2 {
    font-size: 3rem; }
  .festival-content .date {
    color: #f53756;
    font-weight: 700; }
  .festival-content .text {
    line-height: 1.5; }

.lineup {
  background-color: #f53756;
  padding: 5rem 0; }
  .lineup h3,
  .lineup p {
    text-align: center;
    color: #f7f7f7; }
  .lineup .day {
    text-transform: uppercase;
    margin: 5rem 0; }

@media (min-width: 768px) {
  .stage-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem; } }

.stage-content .stage {
  padding-top: 5rem; }

.stage-content .stage-name {
  color: #161616;
  font-size: 2rem;
  font-weight: 300; }
  .stage-content .stage-name:after {
    content: "";
    width: 20rem;
    height: 10rem;
    display: block;
    margin: 2rem auto 0 auto;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center; }

.edm .stage-name::after {
  background-image: url(../img/icono_edm.png); }

.rock .stage-name::after {
  background-image: url(../img/icono_rock.png); }

.calendar {
  padding: 0;
  list-style: none; }
  .calendar li {
    padding: 2.5rem;
    font-weight: 700; }
  .calendar span {
    font-weight: 400; }

.bg-green .calendar li:nth-child(2n + 1) {
  background-color: #3b9691; }

.bg-yellow .calendar li:nth-child(2n + 1) {
  background-color: #fef03a; }

.gallery {
  padding: 5rem 0; }
  .gallery h3 {
    text-align: center; }

.image-gallery {
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem; }
  @media (min-width: 768px) {
    .image-gallery {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 2.5rem; } }
  @media (min-width: 1200px) {
    .image-gallery {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 2.5rem; } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }

.btn-close {
  width: 5rem;
  height: 5rem;
  background-color: #f7f7f7;
  display: grid;
  font-weight: 900;
  place-items: center;
  font-size: 3rem;
  border-radius: 50%; }
  .btn-close:hover {
    cursor: pointer; }

.tickets {
  padding: 5rem 0; }
  .tickets h3 {
    text-align: center;
    color: #f7f7f7; }
  .tickets .container-tickets {
    margin-top: 2.5rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem; }
  .tickets .plan p {
    color: #f7f7f7;
    font-size: 3rem;
    text-align: center;
    margin-bottom: 2rem; }

.pass {
  width: 100%;
  border-spacing: 0; }
  .pass thead {
    background-color: #f53756; }
    .pass thead th {
      padding: 1.25rem;
      color: #f7f7f7;
      text-transform: uppercase;
      font-weight: 300;
      font-size: 2rem; }
  .pass tbody {
    background-color: #f7f7f7; }
    .pass tbody td {
      padding: 1.25rem;
      border-bottom: 1px solid #e1e1e1; }
    .pass tbody tr:last-of-type td {
      border-bottom: none; }
  .pass tfoot {
    background-color: #752f97; }
    .pass tfoot td {
      padding: 1.25rem;
      text-align: center;
      color: #f7f7f7; }

.footer {
  background-color: #f53756;
  padding: 2.5rem;
  text-align: center; }
  .footer p {
    margin: 0;
    color: #f7f7f7; }
