.gallery {
  padding: $separation 0;

  h3 {
    text-align: center;
  }
}

.image-gallery {
  padding: 0;
  list-style: none;

  @include grid(2, $separation);

  @include tablet {
    @include grid(4, $separation / 2);
  }

  @include desktop {
    @include grid(6, $separation / 2);
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-close {
  width: 5rem;
  height: 5rem;
  background-color: $white;
  display: grid;
  font-weight: $fontBlack;
  place-items: center;
  font-size: 3rem;
  border-radius: 50%;

  &:hover {
    cursor: pointer;
  }
}
